
@media screen and (max-width:992px) {
    .App nav , header,.main-about,.main-blogs,.mainprotfolio,.pricing-section,footer{
        padding: 0px 50px;
    }
    header{
        margin-top: 50px;
        display: block;
        height: 120vh;
    } 
    header .with-img img{
        height: 50%;
        width: 50%;
        margin-top: 30px;
    }
    .about{
        display: block;
        padding: 20px; 
    }
    .about img{
         display: none;
    }
    .main-pricing{
        grid-gap: 15px;
      }
}

@media screen and (max-width:768px) {
    .blogs{
        grid-template-columns: 1fr;
      }
      .protfolios {
        grid-template-columns: 1fr 1fr;
      }
      .main-pricing{ 
        grid-template-columns: 1fr 1fr; 
        grid-gap: 40px; 
      }
}


@media screen and (max-width:556px){
    header .with-img img{
        width: 90%;
    }
    .protfolios {
        grid-template-columns: 1fr;
        margin-bottom: 40px;
      }
      .main-pricing{ 
        grid-template-columns: 1fr; 
        grid-gap: 40px; 
      }
}
@media screen and (max-width:430px){
  header,.main-about,.main-blogs,.mainprotfolio,.pricing-section,footer{
    padding: 0px 20px;
  }
  .bx-menu{
    display: none;
  }
  .App nav {
    padding: 10px 20px;
  }
  
}