@import url('https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap');


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style-type: none;
}
body{
  background: #000;
}


.App nav{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 100px;
  background: #4d4d4d58;
  color: #fff;
}

.App nav .logo{
  font-size: 40px ;
  font-weight: 600;
  font-family:  arial;
  color: #FF0060;
  cursor: pointer;
  font-family: 'Permanent Marker', cursive;
}
/* .App nav .logo img{
  width: 100px;
  height: 100px;
} */
.App nav ul{
  /* display: flex; */
  display: none;
  list-style-type: none;
  position: absolute;
  /* width: 100%; */
  top: 70px;
  right: 300px;
 background: #000;
}
.App nav ul li{
  padding: 10px 20px;
  font-size: 20px;
  cursor: pointer;
  transition: ease-in 0.5s;
  text-transform: capitalize;
  display: block;
}
.App nav ul li:hover{
  color:#FF0060;

}
.App nav img{
  height: 40px;
  width: 40px;
  border-radius: 50%;
  cursor: pointer;
  /* box-shadow: 5px 5px 5px #FF0060; */
  animation: identifier infinite 1s ;
}

.App nav .users-all-icon{
  display: flex;
  justify-content: center;
  align-items: center;
}
.users-all-icon i{
  font-size: 40px;
  margin-right: 15px;
  cursor: pointer;
}
.App nav .users-all-icon a{
  padding: 10px;
  color: #fff;
  transition: 0.5s ;
  animation: identifier infinite 1s ;
  height: 50px;
  width: 50px ;
  border-radius: 50%;
  display: flex;
  text-decoration: none;
  margin-left: 10px;
}
.App nav .users-all-icon a i{
  font-size: 30px;
}
.App nav .users-all-icon a:hover{
  color: #FF0060;
}
.bxl-github{
  margin-right: 10px;
}

@keyframes identifier {
  0%{
    box-shadow: 0px 0px 0px red;
  }
  50%{
    box-shadow: 0px 0px 8px red;
  }
  100%{
    box-shadow: 0px 0px 15px red;
  }
}

.bx-menu{
  position: relative;
}






/* headers ------------------- */

header {
  padding: 20px 100px;
  height: 90vh;
  display: grid;
  grid-gap: 20px;
  grid-template-columns: 1fr 1fr;
  justify-content: space-between;
  align-items: center;
 
}
header .collumn{
  color: #fff;
  border-radius: 10px;
}
header .collumn h2{
  font-size: 80px;
  color: #FF0060;
  margin-bottom: 20px;
  animation:headerText 1 1.5s;
}

@keyframes headerText {
    0%{
      font-size: 1px;
      transform: rotate(90deg);
    }
    100%{
      font-size: 80px;
    }
}

header .collumn h3 span{
  color: #FF0060;
}
header .collumn p{
  margin-bottom: 20px;
}
header .collumn a{
  padding: 10px ;
  background: #FF0060;
  color: #fff;
  font-size: 20px;
  font-weight: bold;
  text-decoration: none;
  display: inline-block;
  border-radius: 5px;
  animation: identifier infinite 1.5s ;
}


header .with-img img{
  width: 90%;
  height: 90%;
  border-radius: 50%;
  box-shadow: 0px 0px 10px #FF0060;
}


/* abouts ---------------- */

.main-about{
  padding: 20px 100px;
}

.about{
  border-radius: 10px;
  padding: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
  justify-content: space-between;
  align-items: center;
  background: #20262E;
}
.about img{
  border-radius: 10px;
  /* box-shadow: 0px 0px 10px #FF0060; */
}
.about .about-collamn h2{
  font-size: 40px;
  color: #fff;
}
.about .about-collamn p{
  color: #b4b4b4;
  margin-top: 20px;
}
.about .about-collamn a{
  padding: 10px ;
  background: #FF0060;
  color: #fff;
  font-size: 20px;
  font-weight: bold;
  text-decoration: none;
  display: inline-block;
  border-radius: 5px;
  margin-top: 20px;
  animation: identifier infinite 1.5s ;

}

/* blogs------------------ */

.main-blogs{
  padding: 20px 100px;
}
.blogs-header h2{
  color: #FF0060;
  font-size: 40px;
  text-align: center;
  margin: 20px 0;
  text-decoration: underline;
}

.blogs{
  color: #fff;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
}
.blogs .col{
  background: #20262E;
  padding: 20px;
  border-radius: 10px;
}
.blogs .col h2{
  text-align: center;
  margin-bottom: 20px;
}
.blogs .col img{
  width: 100%;
  height: 300px;
  border-radius: 10px;
}

.blogs .col a{
  padding: 10px ;
  background: #FF0060;
  color: #fff;
  font-size: 20px;
  font-weight: bold;
  text-decoration: none;
  display: inline-block;
  border-radius: 5px;
  margin-top: 20px;
  animation: identifier infinite 1.5s ;
}

/* protfolios -------------------*/

.mainprotfolio{
  padding: 50px 100px;
}
.protfolio-heading{
  color: #FF0060;
  font-size: 40px;
  text-align: center;
  margin: 20px 0;
  text-decoration: underline;
}
.protfolios {
  background: #20262E;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  padding: 20px;
  border-radius: 10px;
}
.protfolios .col img{
  width: 100%;
  border-radius: 5px;
  opacity: 0.9;
}


/* pricing-section ------------ */

.pricing-section{
  padding: 20px 100px;
}
.pricing-header h2{
  font-size: 50px;
  color: #FF0060;
  text-align: center;
  text-decoration: underline;
}

.main-pricing{
  margin: 40px 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 50px;
}
          
.main-pricing .col{
  background: #4d4d4d58;
  color: #fff;
  padding: 20px;
  text-align: center;
  border-radius: 10px;
  transition: 0.5s;
}
.main-pricing .col:hover{
  transform: scale(1.1);
}

.main-pricing .col h2{
  color: #ffffff;
  font-size: 30px;
  color: #0079FF;
  font-family: 'Permanent Marker', cursive;
}
.main-pricing .col h3{
  margin: 10px 0;
  font-size: 80px;
  color: #FF0060;
}

.main-pricing .col p{
  padding: 5px;
  background: #FF0060;
  border-radius: 5px;
}
.main-pricing .col ul{
  margin: 15px 0;
  text-align-last: left;
}
.main-pricing .col ul li{
  line-height: 2;
  color: #ffffff;
}
.main-pricing .col ul li i{
  margin-right: 5px;
  color: #0079FF;
}
.main-pricing .col a{
  padding: 10px ;
  background: #FF0060;
  color: #fff;
  font-size: 20px;
  font-weight: bold;
  text-decoration: none;
  display: inline-block;
  border-radius: 5px;
  margin-top: 20px;
  animation: identifier infinite 1.5s ;
}

footer{
  padding: 20px 100px;
  color: #fff;
  display: flex;
  justify-content: center;
  background: #20262E;
}

footer div{
  width: 500px;
  text-align: center;
}
footer div h2{
  font-size: 40px;
  color: #FF0060;
}
footer div p{
  margin-bottom: 10px;
}